import Api from '../../api/user';


export default {

	onLoad:function(){
		Api.getUserInfo().then(res => {
				console.log(res)
				this.bkName = res.data.name;
			})
	},
	data() {
		return {
			bkName: '',
		};
	},
	methods: {
		logout(){
			Api.logout().then(()=>{
				uni.setStorageSync('token',null)
				uni.setStorageSync('role',null)
				uni.reLaunch({
					url: '../login/login'
				})
			})
		}
	}
}
